import React from 'react'
import bg from './bg.svg'
import { Navbar, Nav, Container,NavDropdown  } from 'react-bootstrap'
import { Carousel } from 'react-bootstrap'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'


export default function LandingPage() {
    return (
        <div style = {{maxHeight:"100", background: "bisque"}}>
            
            <Navbar sticky="top" bg="light" expand="sm">
                <Container>
                    <Navbar.Brand href="#home">Golden Ager</Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="me-auto">
                            <Nav.Link href="#home">Home</Nav.Link>
                            <Nav.Link href="#about">About</Nav.Link>
                            <NavDropdown title="Donate" id="basic-nav-dropdown">
                                <NavDropdown.Item href="#donate/money">Donate Money</NavDropdown.Item>
                                <NavDropdown.Item href="#donate/requirements">Donate Requirements</NavDropdown.Item>
                            </NavDropdown>
                        </Nav>
                        <Nav className="ms-auto">
                            <Nav.Link href="#login">Login</Nav.Link>
                            <Nav.Link href="#signup">Sign up</Nav.Link>
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>

   

        </div>

    )
}
